<template>
  <div id="app">
    <div class="hero-section">
      <el-container>
        <el-header>

          <!-- Категории -->

          <el-select v-model="selectedCategory" clearable placeholder="Категория" class="category-filter">
            <el-option
                v-for="category in categories"
                :key="category.id"
                :label="category.label"
                :value="category.id"
            ></el-option>
          </el-select>

          <!-- Строка поиска -->

          <el-input
              placeholder="Введите запрос для поиска..."
              v-model="searchQuery"
              size="large"
              class="search-input"
              clearable
          >
            <el-button slot="append" icon="el-icon-search"></el-button>

          </el-input>

          <!-- Добавление статьи -->

          <el-button v-if="isAdmin" icon="el-icon-plus" @click="openAddModal"></el-button>

        </el-header>

        <!-- Основной контент с карточками статей -->

        <el-main>
          <div class="article-cards">
            <el-row :gutter="20">
              <el-col :span="6" v-for="(article, index) in filteredArticles" :key="index">
                <el-card shadow="hover"
                         :class="['article-card']"
                         :style="{ borderLeft: '3px solid ' + article.categoryColor }"
                         @click.native="openArticleModal(article)">
                  <h3 class="article-title">{{ article.title }}</h3>
                  <p class="article-description">{{ article.description }}</p>
                  <div class="article-meta">
                    <span class="article-category">{{ article.categoryLabel }}</span>
                    <span class="article-date">{{ article.date }}</span>
                  </div>
                </el-card>
              </el-col>
            </el-row>
          </div>
        </el-main>

        <!-- Модальное окно для отображения деталей статьи -->

        <el-dialog :visible.sync="isArticleVisible" width="50%">

          <div v-if="selectedArticle">
            <h2>{{ selectedArticle.title }}</h2>
            <p>{{ selectedArticle.description }}</p>
            <div class="article-modal-meta">
              <span>Категория: {{ selectedArticle.categoryLabel }}</span>
              <span>Дата: {{ selectedArticle.date }}</span>
            </div>

            <div class="article-modal-content">
              <pre v-html="formattedContentHtml"></pre>
            </div>

          </div>

          <span slot="footer" class="dialog-footer">

                             <el-button v-if="selectedArticle.file" type="primary" @click="downloadFile">Скачать пример
                                 <i class="el-icon-upload el-icon-right"></i>
                             </el-button>

                        </span>

        </el-dialog>

        <!-- Модальное окно для добавления статьи -->

        <el-dialog
            title="Добавить заметку"
            width="35%"
            :visible.sync="isAddVisible">

          <el-form
              :rules="rules"
              :model="articleForm"
              ref="articleForm"
              label-width="120px">

            <!-- Заголовок -->

            <el-form-item
                prop="title"
                label="Заголовок">

              <el-input v-model="articleForm.title" clearable></el-input>

            </el-form-item>

            <!-- Подзаголовок -->

            <el-form-item
                prop="subtitle"
                label="Подзаголовок">

              <el-input v-model="articleForm.subtitle" clearable></el-input>

            </el-form-item>

            <!-- Категория -->

            <el-form-item
                prop="category"
                label="Категория">

              <el-select v-model="articleForm.category" clearable placeholder="" class="add-filter">
                <el-option
                    v-for="category in categories"
                    :key="category.id"
                    :label="category.label"
                    :value="category.id"
                ></el-option>
              </el-select>

            </el-form-item>

            <!-- Динамические элементы -->

            <el-form-item
                v-for="(element, index) in articleForm.elements"
                :key="element.key"
                :label="element.label"
                :prop="'elements.' + index + '.value'"
                :rules="getRules(element.type)">

              <!-- Если параграф -->

              <template v-if="element.type === 'paragraph'">

                <el-input v-model="element.value" autosize type="textarea"></el-input>

              </template>

              <!-- Если код -->

              <template v-else-if="element.type === 'code'">

                <el-input v-model="element.value" autosize type="textarea"></el-input>

                <el-popover
                    placement="bottom"
                    width="200"
                    trigger="click"
                    class="delete-button">

                  <el-input
                      v-model="element.fileName"
                      placeholder="Имя файла">
                  </el-input>

                  <el-button slot="reference" class="utility">
                    <i class="el-icon-document"></i>
                  </el-button>

                </el-popover>

              </template>

              <!-- Если изображение -->

              <template v-else-if="element.type === 'image'">

                <el-upload
                    class="avatar-uploader"
                    action="https://b24helper.ru/local/api/loadFile.php"
                    :show-file-list="false"
                    :on-success="(res, file) => handleAvatarSuccess(res, file, index)"
                    :before-upload="beforeAvatarUpload">
                  <img v-if="element.fileSrc" :src="element.fileSrc" class="avatar">
                  <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                </el-upload>

              </template>

              <!-- Кнопка удаления -->
              <span class="delete-button">
                <el-button
                    icon="el-icon-delete"
                    @click.prevent="removeElement(index)"
                    class=" utility">
              </el-button>
              </span>


            </el-form-item>

            <!-- Навигация -->

            <div class="nav">

              <el-button icon="el-icon-plus" @click="addParagraph">Параграф</el-button>

              <el-button icon="el-icon-plus" @click="addCode">Код</el-button>

              <el-button icon="el-icon-plus" @click="addImage">Изображение</el-button>

              <el-button icon="el-icon-document" @click="triggerFileInput"></el-button>

              <el-tooltip v-if="articleForm.fileSrc" class="item" effect="dark" :content="articleForm.fileName" placement="top-start">
                <el-button icon="el-icon-delete" @click="removeFileInput"></el-button>
              </el-tooltip>

              <el-button type="success" @click="submitForm('articleForm')">Сохранить</el-button>

              <input type="file" ref="fileInput" style="display: none" @change="handleFileChange"/>

            </div>

          </el-form>

        </el-dialog>

      </el-container>
    </div>

    <!-- Подвал -->

    <footer class="footer">
      <a :href="telegramLink" target="_blank" rel="noopener noreferrer" class="telegram-link">
        <i class="fab fa-telegram-plane"></i>
        <span class="username">{{ username }}</span>
      </a>
    </footer>
  </div>
</template>

<script>

import Prism from 'prismjs';
import 'prismjs/themes/prism.css';
import 'prismjs/components/prism-json';


export default {
  data: function() {
    return {
      isAdmin: true,
      searchQuery: '',
      selectedCategory: '',
      articles: [],
      categories: [],
      observer: null,
      isArticleVisible: false,
      isAddVisible: false,
      selectedArticle: {
        categoryLabel: '',
        content: '',
        date: '',
        description: '',
        file: '',
        id: '',
        title: '',
      },
      telegramLink: 'https://t.me/Zammensiny',
      username: '@Zammensiny',
      articleForm: {
        elements: [],
        title: '',
        subtitle: '',
        category: '',
        fileSrc: '',
        fileId: '',
        fileName: '',
      },
      imageUrl: '',
      rules: {
        title: [
          { required: true, message: 'Заголовок не может быть пустым', trigger: 'blur' },
        ],
        subtitle: [
          { required: true, message: 'Подзаголовок не может быть пустым', trigger: 'blur' },
        ],
        category: [
          { required: true, message: 'Категория не может быть пустой', trigger: 'blur' },
        ],
      }

    };
  },
  created() {

    /*-- Получаем статьи при загрузке --*/

    this.fetchArticles();

    /*-- Получаем категории --*/

    this.fetchCategories();

  },

  computed: {

    /*-- Форматирование JSON --*/

    formattedContentHtml() {

      if (this.selectedArticle && this.selectedArticle.content) {
        try {
          const jsonContent = JSON.parse(this.selectedArticle.content);
          const formattedJson = JSON.stringify(jsonContent, null, 2);
          return `<code class="language-json">${Prism.highlight(formattedJson, Prism.languages.json, 'json')}</code>`;
        } catch (e) {
          return `<code class="language-json">${this.selectedArticle.content}</code>`;
        }
      }

      return '';
    },

    /*-- Фильтр по статьям --*/

    filteredArticles() {
      return this.articles.filter(article => {
        const query = this.searchQuery.toLowerCase();
        const titleMatches = article.title.toLowerCase().includes(query);
        const descriptionMatches = article.description.toLowerCase().includes(query);
        const matchesSearchQuery = titleMatches || descriptionMatches;

        const matchesCategory = !this.selectedCategory || article.categoryId === this.selectedCategory;
        return matchesSearchQuery && matchesCategory;
      });
    }
  },

  methods: {

    /*-- Сохранить в буфер --*/

    copyToClipboard() {
      const content = this.selectedArticle.content || '';
      navigator.clipboard.writeText(content).then(() => {
        this.$message.success('Код скопирован!');
      }).catch(err => {
        this.$message.error('Ошибка при копировании кода');
      });
    },

    /*-- Скачать файл --*/

    downloadFile() {

      if (this.selectedArticle && this.selectedArticle.file) {
        const link = document.createElement('a');
        link.href = this.selectedArticle.file;
        link.download = '';
        document.body.appendChild(link);
        link.click();
        document.body.removeChild(link);
      }

    },

    /*-- Получить статьи --*/

    async fetchArticles() {
      try {
        const response = await fetch('https://b24helper.ru/local/api/getArticles.php', {
          method: 'GET',
          headers: {
            'Content-Type': 'application/json',
          },
        });

        const data = await response.json();
        this.articles = data;

      } catch (error) {

        this.$message.error('Произошла ошибка при получении данных', error);

      }
    },

    /*-- Получить категории --*/

    async fetchCategories() {
      try {

        const response = await fetch('https://b24helper.ru/local/api/getCategories.php', {
          method: 'GET',
          headers: {
            'Content-Type': 'application/json',
          },
        });

        const data = await response.json();
        this.categories = data;

      } catch (error) {

        this.$message.error('Произошла ошибка при получении данных', error);

      }
    },

    /*-- Детальная модалка статьи --*/

    openArticleModal(article) {

      const id = article.id;

      fetch('https://b24helper.ru/local/api/getArticle.php', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          id: id
        })
      })
          .then(response => response.json())
          .then(data => {

            this.selectedArticle = data;

            this.isArticleVisible = true;

            this.$nextTick(() => {
              Prism.highlightAll();
            });

          })
          .catch(error => {

            this.$message.error('Произошла ошибка при открытии статьи ', error);

          });

    },

    /*-- Модалка добавления статьи --*/

    openAddModal() {
      this.isAddVisible = true;
    },

    /*-- Валидация для формы --*/

    getRules(type) {

      switch (type) {

        case 'paragraph':

          return [
            { required: true, message: 'Параграф не может быть пустым', trigger: 'blur' }
          ];

        case 'code':

          return [
            { required: true, message: 'Код не может быть пустым', trigger: 'blur' }
          ];

        case 'image':

          return [
            { required: false }
          ];

        default:
          return [
            { required: true, message: 'Поле не может быть пустым', trigger: 'blur' }
          ];

      }
    },

    /*-- Добавление статьи --*/

    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {

          this.addArticle(formName);

        } else {

          this.$message.error('Произошла ошибка при сохранении');
          return false;
        }
      });
    },

    /*-- Удалить элемент из формы --*/

    removeElement(index) {

      /*-- Если тип инпута "Изображение" --*/

      if (this.articleForm.elements[index].type === 'image') {
        const fileId = this.articleForm.elements[index].fileId;

        fetch('https://b24helper.ru/local/api/removeFile.php', {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
          },
          body: JSON.stringify({ fileId: fileId })
        })
            .then(response => response.json())
            .then(data => {
              if (data.status === 'success') {

                this.articleForm.elements.splice(index, 1);

              }
            })
            .catch(error => {

              this.$message.error('Произошла ошибка при запросе на удаление файла ', error);

            });

      } else {

        this.articleForm.elements.splice(index, 1);
      }

    },

    /*-- Добавить параграф --*/

    addParagraph() {
      this.articleForm.elements.push({
        type: 'paragraph',
        key: Date.now(),
        value: '',
        label: 'Параграф'
      });
    },

    /*-- Добавить код --*/

    addCode() {
      this.articleForm.elements.push({
        type: 'code',
        key: Date.now(),
        value: '',
        label: 'Код',
        fileName: '',
      });
    },

    /*-- Добавить изображение --*/

    addImage() {
      this.articleForm.elements.push({
        type: 'image',
        key: Date.now(),
        label: 'Изображение',
        fileSrc: '',
        fileId: '',
      });
    },

    /*-- Обработчик после загрузки изображения --*/

    handleAvatarSuccess(res, file, index) {

      if (res.status === 'success') {

        this.articleForm.elements[index].fileSrc = res.fileSrc;
        this.articleForm.elements[index].fileId = res.fileId;

      } else {

        this.$message.error('Ошибка загрузки изображения');
      }

    },

    /*-- Обработчик перед загрузкой изображения --*/

    beforeAvatarUpload(file) {

      const isJpgOrPng = file.type === 'image/jpeg' || file.type === 'image/png';
      const isLt2M = file.size / 1024 / 1024 < 2;

      if (!isJpgOrPng) {
        this.$message.error('Можно загружать только файлы форматов JPG или PNG');
        return false;
      }

      if (!isLt2M) {
        this.$message.error('Размер изображения не должен превышать 2MB');
        return false;
      }

      return true;

    },

    /*-- Обработчик на закрепление файла к статье --*/

    triggerFileInput() {
      this.$refs.fileInput.click();
    },

    /*-- Обработчик на удаление закрепленного файла к статье --*/

    removeFileInput() {

      const fileId = this.articleForm.fileId;

      fetch('https://b24helper.ru/local/api/removeFile.php', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({ fileId: fileId })
      })
          .then(response => response.json())
          .then(data => {

            if (data.status === 'success') {

              this.$message.success('Файл успешно удален');

              this.$refs.fileInput.value = '';
              this.articleForm.fileSrc = '';
              this.articleForm.fileId = '';
              this.articleForm.fileName = '';

            } else {

              this.$message.error('Ошибка при удалении файла');

            }
          })
          .catch(error => {

            this.$message.error('Произошла ошибка при запросе на удаление файла: ', error);

          });

    },

    /*-- Обработчик при выборе файла закрепленного к статье --*/

    handleFileChange(event) {
      const file = event.target.files[0];
      if (file) {
        this.addFile(file);
      }
    },

    /*-- Обработчик добавления файла --*/

    addFile(file) {

      const allowedExtensions = /\.(rar|7z|zip)$/i;
      const isValidFile = allowedExtensions.test(file.name);

      if (!isValidFile) {
        this.$message.error('Можно загружать только файлы формата .rar .7z или .zip');
        return;
      }

      const formData = new FormData();
      formData.append('file', file);

      fetch('https://b24helper.ru/local/api/loadFile.php', {
        method: 'POST',
        body: formData
      })
          .then(response => response.json())
          .then(data => {
            if (data.status === 'success') {

              this.articleForm.fileSrc = data.fileSrc;
              this.articleForm.fileId = data.fileId;
              this.articleForm.fileName = file.name;

              this.$message.success('Файл успешно загружен');

            } else {

              this.$message.error('Ошибка загрузки файла: ' + data.message);

            }
          })
          .catch(error => {

            this.$message.error('Ошибка при отправке файла');
          });

    },

    /*-- Сброс формы добавления статьи --*/

    resetForm(formName) {

      this.$refs[formName].resetFields();

      this.articleForm.elements = [];
    },

    /*-- Обработчик добавления статьи --*/

    addArticle(formName) {

      const formData = this.articleForm;

      fetch('https://b24helper.ru/local/api/addArticle.php', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          data: formData
        })
      })
          .then(response => response.json())
          .then(data => {

            this.fetchArticles();

            this.resetForm(formName);

            this.isAddVisible = false;

            this.$message.success('Статья успешно добавлена');

          })
          .catch(error => {

            this.$message.error('Произошла ошибка при добавлении статьи ', error);

          });

    }
  },
}
</script>



